.commission-carousel {
  .owl-dot.active span {
    background-color: $primary-color !important;
  }
  .owl-dots {
    margin-top: 25px !important;
  }
}


//hero-carousal
.owl-nav button.owl-prev {
  position: absolute;
  top: 10px;
  right: 36px;
  outline: 0;
  font-size: 30px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  span {
    font-size: 30px;
    color: #000000;
  }
}
.owl-theme .owl-nav [class*="owl-"] {
  width: 48px;
  height: 48px;
  background-color: #ffffff !important;
  color: $main-color;
  border-radius: 50%;
  box-shadow: $main-shadow;
  top: 50%;
}
.owl-nav button.owl-next {
  position: absolute;
  top: 10px;
  left: 36px;
  outline: 0;
  font-size: 30px;
  span {
    font-size: 30px;
    color: #000000;
  }
}

.owl-nav button.owl-prev span,
.owl-nav button.owl-next span {
  font-size: 40px;
  display: block;
  position: relative;
  top: -3px;
}
